import React, { useState, useCallback, useEffect, useContext } from "react";
import {
  ledgerContext,
  LedgerContextValue,
  accountsContext,
  AccountsContextValue,
} from "app/contexts/data";
import { LedgerEntry, getSheet } from "app/api/ledger";
import { BudgetAccount, getAccounts } from "app/api/accounts";
import { googleApiConfigContext } from "app/contexts/googleApi";

export const useLedgerData = (): LedgerContextValue => {
  const [rowsData, setRowsData] = useState<LedgerEntry[]>([]);
  const { clientId, spreadsheetId } = useContext(googleApiConfigContext);

  const loadSheets = useCallback(() => {
    if (!clientId || !spreadsheetId) {
      return;
    }
    getSheet(clientId, spreadsheetId).then((entries) => {
      setRowsData(entries);
    });
  }, [setRowsData]);

  useEffect(loadSheets, []);

  return [rowsData, loadSheets];
};

export const useAccountsData = (): AccountsContextValue => {
  const [accounts, setAccounts] = useState<BudgetAccount[]>([]);

  const { clientId, spreadsheetId } = useContext(googleApiConfigContext);
  const loadAccounts = useCallback(() => {
    if (!clientId || !spreadsheetId) {
      return;
    }
    getAccounts(clientId, spreadsheetId).then((accounts) => {
      setAccounts(accounts);
    });
  }, [setAccounts]);

  return [accounts, loadAccounts];
};

const SheetsDataProvider: React.FC<{}> = ({ children }) => {
  const ledgerContextValue = useLedgerData();
  const accountsContextValue = useAccountsData();
  return (
    <accountsContext.Provider value={accountsContextValue}>
      <ledgerContext.Provider value={ledgerContextValue}>
        {children}
      </ledgerContext.Provider>
    </accountsContext.Provider>
  );
};

export default SheetsDataProvider;
